* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f5f5;
  color: #333;
}
.calculator {
  width: 35%;
  margin: 50px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
h1 {
  text-align: center;
  margin-bottom: 20px;
}
#display {
  width: 100%;
  height: 50px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 24px;
  text-align: right;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #e9e9e9;
}
.number button {
  width: 22%;
  height: 50px;
  margin: 1%;
  font-size: 18px;
  border: none;
  border-radius: 4px;
  background-color: #f0f0f0;
  cursor: pointer;
}
.number button:hover {
  background-color: #dcdcdc;
}
.operator button {
  width: 22%;
  height: 50px;
  margin: 1%;
  font-size: 18px;
  border: none;
  border-radius: 4px;
  background-color: #ff9500;
  color: #fff;
  cursor: pointer;
}
.operator button:hover {
  background-color: #e08900;
}
#equals {
  width: 96%;
  height: 50px;
  margin: 1%;
  font-size: 18px;
  border: none;
  border-radius: 4px;
  background-color: #34c759;
  color: #fff;
  cursor: pointer;
}
#equals:hover {
  background-color: #28a745;
}
#clear {
  width: 96%;
  height: 50px;
  margin: 1%;
  font-size: 18px;
  border: none;
  border-radius: 4px;
  background-color: #ff3b30;
  color: #fff;
  cursor: pointer;
}
#clear:hover {
  background-color: #e02c20;
}
@media screen and (max-width: 400px) {
  .calculator {
    width: 90%;
    padding: 10px;
  }
  #display {
    height: 40px;
    font-size: 20px;
  }
  .number button,
  .operator button,
  #equals,
  #clear {
    height: 40px;
    font-size: 16px;
  }
}
